export const VERSION = "1.0.0.37";

// The initialize function must be run each time a new page is loaded
Office.onReady(() => {
  console.log("Office.onReady");
  console.log("Manifest Version:", VERSION);
});

document.addEventListener("DOMContentLoaded", async (): Promise<void> => {
  const lRowOption = document.getElementById("rowOption") as HTMLInputElement;
  const lColumnOption = document.getElementById("columnOption") as HTMLInputElement;
  // Loading saved value
  const lStorageValue = localStorage.getItem('orientationMode') ?? "column";

  console.log("TASKPANE orientationMode from storage:", lStorageValue);

  if (lStorageValue === "column") {
    lColumnOption.checked = true;
  } else {
    lRowOption.checked = true;
  }

  lRowOption.addEventListener("change", async (): Promise<void> => {
    try {
      await onRadioButtonChange(lRowOption);
    } catch (e) {
      console.error("Error switch on rowMode:", e);
    }

  });

  lColumnOption.addEventListener("change", async (): Promise<void> => {
    try {
      await onRadioButtonChange(lColumnOption);
    } catch (e) {
      console.error("Error switch on columnMode:", e);
    }
  });
});

export async function onRadioButtonChange(aHTMLInputElement: HTMLInputElement): Promise<void> {
  let lOrientationMode: "row" | "column" = aHTMLInputElement.value as "row" | "column";

  if (aHTMLInputElement.checked) {
    localStorage.setItem('orientationMode', lOrientationMode);
    console.log("OrientationMode saved in localStorage:", lOrientationMode);
  }

  console.log("onRadioButtonChange before Excel.run:");

  // Recalculate function
  await Excel.run(async (context: Excel.RequestContext): Promise<void> => {
    console.log("onRadioButtonChange IN Excel.run:");
    context.workbook.application.calculate(Excel.CalculationType.full);
    await context.sync();
  }).catch(error => {
    console.error("Error Excel.run:", error);
  });
}